.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.buttons-container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.calendar-container {
  display: flex;
  justify-content: center;
  margin: 15px;
  padding: 20px;
}

.department-button {
  padding: 20px;
  border: none;
  background-color: transparent;
  border: 2px solid black;
  margin: 1rem;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
}

.holidayCalendarStyle {
  color: red !important;
}

.nonHolidayCalendarStyle {
  color: black !important;
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 50%;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  margin: 0.5em;
  font-size: 0.8em;
}

.modal-header > button {
  align-self: center;
  border-radius: 10%;
  height: 50%;
  font-size: large;
  background-color: transparent;
  border: 2px solid black;
  color: black;
}

@media screen and (max-width: 500px) {
  .department-button {
    margin: 5px;
  }
}
